nav{
    background:rgba(0,0,0,0.3) ;
    width: max-content;
    padding: 1rem;
    display: block;
    position:fixed;
    left: 50%;
    transform:translate(-50%);
    bottom:2rem;
    display: flex;
    gap: 1rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    z-index: 100;
}

nav a{
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-primary);
    font-size: 1.1rem;
}

nav a:hover{
    background:rgba(0,0,0,0.3);

}

nav a.active{
    background: var(--color-primary);
    color: var(--color-white);
}